<template>
	<view class="page">
		<!-- È°µÈù¢Â§¥ÈÉ® -->
		<view class="page-header">
			<view class="header-content">
				<text class="header-title">ÈáçÁΩÆÂØÜÁ†Å</text>
			</view>
		</view>
		
		<!-- Ë°®ÂçïÂç°Áâá -->
		<view class="form-card">
			<!-- ÂæÆ‰ø°Âè∑ -->
			<view class="form-item">
				<view class="form-label">
					<text class="label-text">ÂæÆ‰ø°Âè∑</text>
					<text class="required">*</text>
				</view>
				<input 
					class="form-input"
					v-model="form.wechatId"
					placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÂæÆ‰ø°Âè∑"
					:disabled="step > 1"
				/>
				<view class="form-hint">
					<text class="hint-text">ËØ∑ËæìÂÖ•ÊÇ®Ê≥®ÂÜåÊó∂‰ΩøÁî®ÁöÑÂæÆ‰ø°Âè∑</text>
				</view>
			</view>
			
			<!-- È™åËØÅÁ†Å -->
			<view class="form-item" v-if="step >= 2">
				<view class="form-label">
					<text class="label-text">È™åËØÅÁ†Å</text>
					<text class="required">*</text>
				</view>
				<view class="code-input-row">
					<input 
						class="form-input code-input"
						v-model="form.verifyCode"
						placeholder="ËØ∑ËæìÂÖ•6‰ΩçÈ™åËØÅÁ†Å"
						type="number"
						maxlength="6"
					/>
					<view class="send-code-btn" @click="sendVerifyCode" :class="{ 'disabled': countdown > 0 }">
						<text class="code-btn-text">{{ countdown > 0 ? `${countdown}ÁßíÂêéÈáçÂèë` : 'ÂèëÈÄÅÈ™åËØÅÁ†Å' }}</text>
					</view>
				</view>
				<view class="form-hint">
					<text class="hint-text">È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅÂà∞ÊÇ®ÁöÑÂæÆ‰ø°ÔºåËØ∑Êü•Êî∂</text>
				</view>
			</view>
			
			<!-- Êñ∞ÂØÜÁ†Å -->
			<view class="form-item" v-if="step >= 3">
				<view class="form-label">
					<text class="label-text">Êñ∞ÂØÜÁ†Å</text>
					<text class="required">*</text>
				</view>
				<input 
					class="form-input"
					type="password"
					v-model="form.newPassword"
					placeholder="ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†ÅÔºà‰∏çÂ∞ë‰∫é8‰ΩçÔºåÂåÖÂê´Â§ßÂ∞èÂÜôÂ≠óÊØçÂíåÊï∞Â≠óÔºâ"
					:password="!showNewPassword"
				/>
				<view class="password-toggle" @click="showNewPassword = !showNewPassword">
					<text class="toggle-icon">{{ showNewPassword ? 'üëÅÔ∏è' : 'üëÅÔ∏è‚Äçüó®Ô∏è' }}</text>
				</view>
			</view>
			
			<!-- ÂØÜÁ†ÅÂº∫Â∫¶ÊèêÁ§∫ -->
			<view v-if="step >= 3 && form.newPassword" class="password-tips">
				<view class="tip-item" :class="{ 'valid': hasLength }">
					<text class="tip-icon">{{ hasLength ? '‚úì' : '‚óã' }}</text>
					<text class="tip-text">Ëá≥Â∞ë8‰Ωç</text>
				</view>
				<view class="tip-item" :class="{ 'valid': hasUpperCase }">
					<text class="tip-icon">{{ hasUpperCase ? '‚úì' : '‚óã' }}</text>
					<text class="tip-text">ÂåÖÂê´Â§ßÂÜôÂ≠óÊØç</text>
				</view>
				<view class="tip-item" :class="{ 'valid': hasLowerCase }">
					<text class="tip-icon">{{ hasLowerCase ? '‚úì' : '‚óã' }}</text>
					<text class="tip-text">ÂåÖÂê´Â∞èÂÜôÂ≠óÊØç</text>
				</view>
				<view class="tip-item" :class="{ 'valid': hasNumber }">
					<text class="tip-icon">{{ hasNumber ? '‚úì' : '‚óã' }}</text>
					<text class="tip-text">ÂåÖÂê´Êï∞Â≠ó</text>
				</view>
			</view>
			
			<!-- Á°ÆËÆ§Êñ∞ÂØÜÁ†Å -->
			<view class="form-item" v-if="step >= 3">
				<view class="form-label">
					<text class="label-text">Á°ÆËÆ§Êñ∞ÂØÜÁ†Å</text>
					<text class="required">*</text>
				</view>
				<input 
					class="form-input"
					type="password"
					v-model="form.confirmPassword"
					placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•Êñ∞ÂØÜÁ†Å"
					:password="!showConfirmPassword"
				/>
				<view class="password-toggle" @click="showConfirmPassword = !showConfirmPassword">
					<text class="toggle-icon">{{ showConfirmPassword ? 'üëÅÔ∏è' : 'üëÅÔ∏è‚Äçüó®Ô∏è' }}</text>
				</view>
			</view>
			
			<!-- ÂØÜÁ†ÅÂåπÈÖçÊèêÁ§∫ -->
			<view v-if="step >= 3 && form.confirmPassword && form.newPassword !== form.confirmPassword" class="error-tip">
				<text class="error-icon">‚ö†Ô∏è</text>
				<text class="error-text">‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥</text>
			</view>
		</view>
		
		<!-- Êèê‰∫§ÊåâÈíÆ -->
		<view class="submit-container">
			<view class="submit-btn" @tap="handleNext" v-if="step === 1">
				<text class="submit-text">‰∏ã‰∏ÄÊ≠•ÔºöÂèëÈÄÅÈ™åËØÅÁ†Å</text>
			</view>
			<view class="submit-btn" @tap="handleVerify" v-if="step === 2">
				<text class="submit-text">È™åËØÅÈ™åËØÅÁ†Å</text>
			</view>
			<view class="submit-btn" @tap="handleReset" v-if="step === 3">
				<text class="submit-text">Á°ÆËÆ§ÈáçÁΩÆÂØÜÁ†Å</text>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			step: 1, // 1: ËæìÂÖ•ÂæÆ‰ø°Âè∑, 2: ËæìÂÖ•È™åËØÅÁ†Å, 3: ËæìÂÖ•Êñ∞ÂØÜÁ†Å
			form: {
				wechatId: '',
				verifyCode: '',
				newPassword: '',
				confirmPassword: ''
			},
			showNewPassword: false,
			showConfirmPassword: false,
			countdown: 0,
			countdownTimer: null,
			verifyToken: '' // È™åËØÅÁ†ÅÈ™åËØÅÂêéÁöÑtoken
		}
	},
	
	computed: {
		// ÂØÜÁ†ÅÈïøÂ∫¶Ê£ÄÊü•
		hasLength() {
			return this.form.newPassword.length >= 8
		},
		
		// ÂåÖÂê´Â§ßÂÜôÂ≠óÊØç
		hasUpperCase() {
			return /[A-Z]/.test(this.form.newPassword)
		},
		
		// ÂåÖÂê´Â∞èÂÜôÂ≠óÊØç
		hasLowerCase() {
			return /[a-z]/.test(this.form.newPassword)
		},
		
		// ÂåÖÂê´Êï∞Â≠ó
		hasNumber() {
			return /[0-9]/.test(this.form.newPassword)
		},
		
		// ÂØÜÁ†ÅÂº∫Â∫¶ÊòØÂê¶Êª°Ë∂≥Ë¶ÅÊ±Ç
		isPasswordValid() {
			return this.hasLength && this.hasUpperCase && this.hasLowerCase && this.hasNumber
		}
	},
	
	onUnload() {
		// Ê∏ÖÈô§ÂÄíËÆ°Êó∂
		if (this.countdownTimer) {
			clearInterval(this.countdownTimer)
		}
	},
	
	methods: {
		// È™åËØÅÂæÆ‰ø°Âè∑Ê†ºÂºè
		validateWechatId(wechatId) {
			if (!wechatId) {
				return { valid: false, message: 'ÂæÆ‰ø°Âè∑‰∏çËÉΩ‰∏∫Á©∫' }
			}
			
			// ÂæÆ‰ø°Âè∑Ê†ºÂºèÔºö6-20‰∏™Â≠óÁ¨¶ÔºåÂ≠óÊØç„ÄÅÊï∞Â≠ó„ÄÅ‰∏ãÂàíÁ∫ø„ÄÅÂáèÂè∑
			if (!/^[a-zA-Z0-9_-]{6,20}$/.test(wechatId)) {
				return { valid: false, message: 'ÂæÆ‰ø°Âè∑Ê†ºÂºè‰∏çÊ≠£Á°ÆÔºåËØ∑ËæìÂÖ•6-20‰∏™Â≠óÁ¨¶ÔºàÂ≠óÊØç„ÄÅÊï∞Â≠ó„ÄÅ‰∏ãÂàíÁ∫ø„ÄÅÂáèÂè∑Ôºâ' }
			}
			
			return { valid: true, message: 'Ê†ºÂºèÊ≠£Á°Æ' }
		},
		
		// È™åËØÅÂØÜÁ†ÅÊ†ºÂºè
		validatePassword(password) {
			if (!password) {
				return { valid: false, message: 'ÂØÜÁ†Å‰∏çËÉΩ‰∏∫Á©∫' }
			}
			
			if (password.length < 8) {
				return { valid: false, message: 'ÂØÜÁ†ÅÈïøÂ∫¶‰∏çËÉΩÂ∞ë‰∫é8‰Ωç' }
			}
			
			if (!/[A-Z]/.test(password)) {
				return { valid: false, message: 'ÂØÜÁ†ÅÂøÖÈ°ªÂåÖÂê´Ëá≥Â∞ë‰∏Ä‰∏™Â§ßÂÜôÂ≠óÊØç' }
			}
			
			if (!/[a-z]/.test(password)) {
				return { valid: false, message: 'ÂØÜÁ†ÅÂøÖÈ°ªÂåÖÂê´Ëá≥Â∞ë‰∏Ä‰∏™Â∞èÂÜôÂ≠óÊØç' }
			}
			
			if (!/[0-9]/.test(password)) {
				return { valid: false, message: 'ÂØÜÁ†ÅÂøÖÈ°ªÂåÖÂê´Ëá≥Â∞ë‰∏Ä‰∏™Êï∞Â≠ó' }
			}
			
			return { valid: true, message: 'ÂØÜÁ†ÅÊ†ºÂºèÊ≠£Á°Æ' }
		},
		
		// ‰∏ã‰∏ÄÊ≠•ÔºöÂèëÈÄÅÈ™åËØÅÁ†Å
		async handleNext() {
			// 1. È™åËØÅÂæÆ‰ø°Âè∑
			const wechatCheck = this.validateWechatId(this.form.wechatId)
			if (!wechatCheck.valid) {
				uni.showToast({
					title: wechatCheck.message,
					icon: 'none',
					duration: 2000
				})
				return
			}
			
			// 2. ÂèëÈÄÅÈ™åËØÅÁ†Å
			await this.sendVerifyCode()
		},
		
		// ÂèëÈÄÅÈ™åËØÅÁ†Å
		async sendVerifyCode() {
			if (this.countdown > 0) {
				return
			}
			
			// Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÊ≠•ÔºåÈúÄË¶ÅÂÖàÈ™åËØÅÂæÆ‰ø°Âè∑
			if (this.step === 1) {
				const wechatCheck = this.validateWechatId(this.form.wechatId)
				if (!wechatCheck.valid) {
					uni.showToast({
						title: wechatCheck.message,
						icon: 'none',
						duration: 2000
					})
					return
				}
			}
			
			uni.showLoading({ title: 'ÂèëÈÄÅ‰∏≠...', mask: true })
			
			try {
				const result = await wx.cloud.callFunction({
					name: 'sendResetPasswordCode',
					data: {
						wechatId: this.form.wechatId
					}
				})
				
				uni.hideLoading()
				
				if (result.result && result.result.success) {
					// Â¶ÇÊûúËøîÂõû‰∫ÜÈ™åËØÅÁ†ÅÔºåÊòæÁ§∫Âú®È°µÈù¢‰∏ä
					if (result.result.verifyCode) {
						uni.showModal({
							title: 'È™åËØÅÁ†ÅÂ∑≤ÁîüÊàê',
							content: `ÊÇ®ÁöÑÈ™åËØÅÁ†ÅÊòØÔºö${result.result.verifyCode}\n\nËØ∑Â¶•ÂñÑ‰øùÁÆ°Ôºå5ÂàÜÈíüÂÜÖÊúâÊïà`,
							showCancel: false,
							confirmText: 'ÊàëÁü•ÈÅì‰∫Ü'
						})
					} else {
						uni.showToast({
							title: 'È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅÂà∞ÊÇ®ÁöÑÂæÆ‰ø°',
							icon: 'success'
						})
					}
					
					// ËøõÂÖ•‰∏ã‰∏ÄÊ≠•
					if (this.step === 1) {
						this.step = 2
					}
					
					// ÂºÄÂßãÂÄíËÆ°Êó∂
					this.startCountdown()
				} else {
					throw new Error(result.result?.message || 'ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•')
				}
			} catch (err) {
				uni.hideLoading()
				console.error('ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•:', err)
				uni.showToast({
					title: err.message || 'ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•',
					icon: 'none',
					duration: 2000
				})
			}
		},
		
		// ÂºÄÂßãÂÄíËÆ°Êó∂
		startCountdown() {
			this.countdown = 60
			this.countdownTimer = setInterval(() => {
				this.countdown--
				if (this.countdown <= 0) {
					clearInterval(this.countdownTimer)
					this.countdownTimer = null
				}
			}, 1000)
		},
		
		// È™åËØÅÈ™åËØÅÁ†Å
		async handleVerify() {
			// 1. È™åËØÅÈ™åËØÅÁ†Å
			if (!this.form.verifyCode || this.form.verifyCode.length !== 6) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•6‰ΩçÈ™åËØÅÁ†Å',
					icon: 'none'
				})
				return
			}
			
			uni.showLoading({ title: 'È™åËØÅ‰∏≠...', mask: true })
			
			try {
				const result = await wx.cloud.callFunction({
					name: 'verifyResetPasswordCode',
					data: {
						wechatId: this.form.wechatId,
						verifyCode: this.form.verifyCode
					}
				})
				
				uni.hideLoading()
				
				if (result.result && result.result.success) {
					// ‰øùÂ≠òÈ™åËØÅtoken
					this.verifyToken = result.result.token
					
					uni.showToast({
						title: 'È™åËØÅÁ†ÅÊ≠£Á°Æ',
						icon: 'success'
					})
					
					// ËøõÂÖ•‰∏ã‰∏ÄÊ≠•
					this.step = 3
				} else {
					throw new Error(result.result?.message || 'È™åËØÅÁ†ÅÈîôËØØ')
				}
			} catch (err) {
				uni.hideLoading()
				console.error('È™åËØÅÈ™åËØÅÁ†ÅÂ§±Ë¥•:', err)
				uni.showToast({
					title: err.message || 'È™åËØÅÁ†ÅÈîôËØØ',
					icon: 'none',
					duration: 2000
				})
			}
		},
		
		// ÈáçÁΩÆÂØÜÁ†Å
		async handleReset() {
			// 1. È™åËØÅÊñ∞ÂØÜÁ†ÅÊ†ºÂºè
			const passwordCheck = this.validatePassword(this.form.newPassword)
			if (!passwordCheck.valid) {
				uni.showToast({
					title: passwordCheck.message,
					icon: 'none',
					duration: 2000
				})
				return
			}
			
			// 2. È™åËØÅÁ°ÆËÆ§ÂØÜÁ†Å
			if (this.form.newPassword !== this.form.confirmPassword) {
				uni.showToast({
					title: '‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥',
					icon: 'none'
				})
				return
			}
			
			// 3. Êèê‰∫§ÈáçÁΩÆ
			uni.showLoading({ title: 'ÈáçÁΩÆ‰∏≠...', mask: true })
			
			try {
				const result = await wx.cloud.callFunction({
					name: 'resetPassword',
					data: {
						wechatId: this.form.wechatId,
						verifyToken: this.verifyToken,
						newPassword: this.form.newPassword
					}
				})
				
				uni.hideLoading()
				
				if (result.result && result.result.success) {
					uni.showToast({
						title: 'ÂØÜÁ†ÅÈáçÁΩÆÊàêÂäü',
						icon: 'success'
					})
					
					// Âª∂ËøüËøîÂõûÁôªÂΩïÈ°µ
					setTimeout(() => {
						uni.reLaunch({
							url: '/pages/login/index'
						})
					}, 1500)
				} else {
					throw new Error(result.result?.message || 'ÂØÜÁ†ÅÈáçÁΩÆÂ§±Ë¥•')
				}
			} catch (err) {
				uni.hideLoading()
				console.error('ÂØÜÁ†ÅÈáçÁΩÆÂ§±Ë¥•:', err)
				uni.showToast({
					title: err.message || 'ÂØÜÁ†ÅÈáçÁΩÆÂ§±Ë¥•',
					icon: 'none',
					duration: 2000
				})
			}
		}
	}
}
</script>

